@import "color.less";
.shopping-cart--container {
    width: 350px;
    outline: @green solid 1px;
    box-shadow: 0px 0px 30px rgba(0,0,0,0.2);
}

.shopping-cart--header {
    background-color: @darkest_cyan;
    text-transform: uppercase;
    color: @white;
    font-weight: bold;
    padding-top: 2px;
    padding-bottom: 2px;
    letter-spacing: 0.8px;
    span:nth-child(1){
      font-size:18px;
    }
    
}

.shopping-cart--content{
  min-height: 0px !important;
}

.scrolling-wrapper {
  position:relative;
}

.shopping-cart-element-wrapper{
  &.shopping-cart-section-separator ::ng-deep .shopping-cart-item-header-border{
    border-bottom: 1px solid @dark_white; 
  }
}



.shopping-cart--footer {
    padding: 10px;
    background-color: @white;
    border-top: 1px solid @green;
    ::ng-deep nbp-button:not(.single-button) .button {
      width: 100% !important;
    }
}

.arrow-container-base {
  cursor:pointer;
  position:absolute !important; 
  z-index:1100;
  height:20px;
  text-align: center;
}

.arrow-up-icon{
  position:absolute;
  border-bottom:15px solid @green;
  border-left:15px solid transparent;
  border-right:15px solid transparent;
  border-top: 0px solid transparent;
}

.shopping-cart--arrow-up {
  .arrow-container-base;
  background-image:linear-gradient(to bottom, rgba(255,255,255,1),rgba(255,255,255,0.5));
  top: 0px; 
  left: 0px;
  
  div.arrow-up{
    .arrow-up-icon;
    height:0px;
    width:0px;
    top:50%;
    left: 50%;
    transform:translate(-50% , -50%);
    position:absolute;
  }
}

.arrow-down-icon {
  position:absolute;
  border-top:15px solid @green;
  border-left:15px solid transparent;
  border-right:15px solid transparent;
  border-bottom: 0px solid transparent;
}

.shopping-cart--arrow-down {
  .arrow-container-base;
  background-image:linear-gradient(to top, rgba(255,255,255,1),rgba(255,255,255,0.5));
  bottom: 0px; 
  left: 0px; 
  cursor:pointer;
  div.arrow-down{
    .arrow-down-icon;
    height:0px;
    width:0px;
    top:50%;
    left: 50%;
    transform:translate(-50% , -50%); 
  }
}
